@use 'base/variables' as  v;
@use 'base/mixins' as  m;

.servicios {
    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        gap: 2rem;
        @include m.tablet {
            flex-direction: row;
        }
    }
    &__card {
        padding: 2rem;
        background-image: radial-gradient(circle, v.$primario 35%, v.$secundario 100%);
        color: v.$blanco;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        gap: 2rem;
        text-align: center;
        border-radius:  0 0 4rem 4rem;
        border: 3px solid v.$negro;
        @include m.tablet {
            width: 20%;
            height: 32rem;
        }
        cursor: pointer;
        transition-property: transform;
        transition-duration: .3s;
        &:hover {
            transform: scale(1.05);
        }
    }
    &__img {
        width: 10rem;
        margin: 0 auto;
    }
    &__SubTitulo {
        font-weight: 700;
        font-size: 1.6rem;
        color: v.$blanco;
    }
    &__precio {
        font-size: 2rem;
        font-weight: 900;
        color: v.$blanco;
    }
}